<template>
  <v-container class="profesionales__container pa-0">
    <validation-observer
      ref="observer"
      v-slot="{ invalid }"
    >
      <form @submit.prevent="validateForm()">
        <div class="profesionales">
          <div class="profesionales__title">
            <h4>Nuestros Terapeutas <v-icon>mdi-play</v-icon></h4>
          </div>

          <div class="profesionales__items">
            <div class="profesionales__items_box">
              <validation-provider
                v-slot="{ errors }"
                rules="required"
                name="Nombre"
              >
                <v-select
                  v-model="sucursal"
                  :error-messages="errors"
                  :items="sucursales"
                  item-value="ToLink"
                  item-text="Nombre"
                  label="Sucursal"
                  solo
                  hide-details
                />
              </validation-provider>
            </div>
            <div class="profesionales__items_box">
              <validation-provider
                v-slot="{ errors }"
                rules="required"
                name="Nombre"
              >
                <v-select
                  v-model="especialidad"
                  :error-messages="errors"
                  :items="especialidades"
                  item-value="ToLink"
                  item-text="NombreCompleto"
                  label="Especialidades"
                  solo
                  hide-details
                  no-data-text="Seleccione primero una sucursal"
                />
              </validation-provider>
            </div>
            <div class="profesionales__items_box">
              <v-text-field
                v-model="nombre"
                label="Nombre Terapeuta"
                solo
                hide-details
              />
            </div>
          </div>

          <div>
            <div class="profesionales__btn">
              <v-btn
                type="submit"
                color="warning"
                large
                :disabled="invalid"
              >
                Buscar
              </v-btn>
            </div>
          </div>
        </div>
      </form>
    </validation-observer>
    <!-- talleres -->
    <slot />
    <!-- talleres -->
  </v-container>
</template>

<script>
  import { mapState } from 'vuex'
  import Vue from 'vue'

  export default {
    name: 'Profesionales',
    data: () => {
      return {
        sucursal: [],
        especialidad: [],
        nombre: '',
        especialidades: [],
      }
    },
    computed: {
      ...mapState('web', ['sucursales']),
    },
    watch: {
      sucursal: function (val) {
        this.getEspecialidadesBySucursal(val)
      },
    },
    methods: {
      getEspecialidadesBySucursal (toLink) {
        Vue.axios
          .get(`/api/especialidades/combos?toLink=${toLink}`)
          .then(response => {
            this.especialidades = response.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
      validateForm () {
        this.$router.push({
          path: `/terapeutas/${this.sucursal}/${this.especialidad}/${this.nombre}`,
        })
      },
    },
  }
</script>
